import moment from 'moment'

window.moment = moment

export default {
  getMonth (date) {
    return moment.utc(date, 'YYYY-MM-dd').local().format('MMMM')
  },
  getShortMonth (date) {
    return moment.utc(date, 'YYYY-MM-dd').local().format('MMM')
  },
  getMonthDay (date) {
    return moment.utc(date).local().format('MMM Do')
  },
  getDayOfMonth (date) {
    return moment.utc(date, 'YYYY-MM-DD').local().format('Do')
  },
  getDayOfWeek (date) {
    return moment.utc(date, 'YYYY-MM-dd').local().format('ddd')
  },
  formatTime (time) {
    return moment.utc(time, 'H:mm').local().format('h:mm A')
  },
  formatFull (dateTime) {
    return moment.utc(dateTime).local().format('MMM Do, h:mm A')
  }
}
